<template>
    <div class="globlgl">
        <div class="btns">
            <el-button class="float-l btn" @click="handleVoipGatewayGroupAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="" :maxlength="255" icon="search" v-model="searchInput" :on-icon-click="handleVoipGatewayGroupSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="voipGatewayGroupData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="">
                <template slot-scope="scope">
                    <el-button @click="handlevoipGatewayGroupCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="groupId" label=""></el-table-column>
            <el-table-column prop="gatewayId" label=""></el-table-column>
            <el-table-column prop="gatewayName" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleVoipGatewayGroupDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleVoipGatewayGroupEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="voipGatewayGroupForm" :rules="formRules" labelPosition="left" ref="voipGatewayGroupForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="voipGatewayGroupForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayGroupForm.groupId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="gatewayId" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayGroupForm.gatewayId" placeholder="">
                        <el-option  v-for="item in allGateway" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleVoipGatewayGroupSave('voipGatewayGroupForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="voipGatewayGroupEditForm" :rules="formRules" labelPosition="left" ref="voipGatewayGroupEditForm">
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="voipGatewayGroupEditForm.vlanId" placeholder="">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="groupId" label="" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayGroupEditForm.groupId" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="gatewayId" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayGroupEditForm.gatewayId" placeholder="">
                        <el-option  v-for="item in allGateway" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleVoipGatewayGroupSave('voipGatewayGroupEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.vlanId}}</div>
            <div class="item">: {{showCheck.gatewayId}}</div>
            <div class="item">: {{showCheck.gatewayName}}</div>
            <div class="item">: {{showCheck.groupId}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    voipGatewayGroupList,
    savevoipGatewayGroup,
    deletevoipGatewayGroupByIds,
    getAllGateway,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';

  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
      };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        voipGatewayGroupData: [],
        showCheck: {}, //
        dialogFormCheck: false, //

        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        voipGatewayGroupForm: {
          vlanId: 0,
          groupId: '',
          gatewayId: '',
          gatewayName: '',
        },
        voipGatewayGroupEditForm: {
          vlanId: 0,
          groupId: '',
          gatewayId: '',
          gatewayName: '',
        },
        formLabelWidth: '80px',
        formRules: {
            vlanId: [{required: true,validator:checkNumber, message:'()',trigger: 'blur'}],
           groupId: [{validator: checkNull, trigger: 'blur'}],
           gatewayId: [{validator: checkNull, trigger: 'blur'}],
         
        },
        allGateway:[],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
        
      }
    },
    created() {
      this.getvoipGatewayGroupList(); //
      this.getAllGateway();
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    methods: {
      // 
      async getvoipGatewayGroupList() {
        try {
          let query = {
            gatewayName: this.searchInput
          }
          //
          const res = await voipGatewayGroupList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.voipGatewayGroupData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                gatewayId:item.gatewayId,
                gatewayName:item.gatewayName,
                groupId:item.groupId,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.voipGatewayGroupData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },

      //
      async getAllGateway() {
        try {
            const res = await getAllGateway();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.gatewayName;
                   temp.value = value.gatewayId;
                   this.$data.allGateway.push(temp);
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('', err);
        }
      },

      handSaveParam(args){
         if(args.groupId == undefined || args.groupId === '')delete args.groupId;
         if(args.gatewayId == undefined || args.gatewayId === ''){
            delete args.gatewayId;
         }
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getvoipGatewayGroupList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleVoipGatewayGroupSearch() {
        this.getvoipGatewayGroupList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //
      handleVoipGatewayGroupAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        this.voipGatewayGroupForm = {
          groupId: '',
          gatewayId: '',
          gatewayName: '',
          vlanId: tempVlanId,
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deletevoipGatewayGroupByIds(data);
              if (result.resultCode == '0') {
                that.getvoipGatewayGroupList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleVoipGatewayGroupDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deletevoipGatewayGroupByIds(data);
            if (result.resultCode == '0') {
              that.getvoipGatewayGroupList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleVoipGatewayGroupEdit(index, row) {
        if (typeof row !== 'undefined') {
          this.voipGatewayGroupEditForm.vlanId = row.vlanId;
          this.voipGatewayGroupEditForm.groupId = row.groupId;
          this.voipGatewayGroupEditForm.gatewayId = row.gatewayId;
          this.voipGatewayGroupEditForm.gatewayName = row.gatewayName;
          this.voipGatewayGroupEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //
      handlevoipGatewayGroupCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //  
      handleVoipGatewayGroupSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savevoipGatewayGroup(params);
              if (result.resultCode == '0') {
                  if(formName == 'voipGatewayGroupForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getvoipGatewayGroupList();
                  this.$message({
                     type: 'success',
                     message: ''
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: ''
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>


